<template>
  <div id="dashboard">
     <ul class="connection with-header">
       <li class="collection-header"><h4>Employees</h4>
       </li>
       <li v-for="employee in employee" v-bind:key="employee.id"
       class="collection-item">
       <div class="chip">{{employee.department}}</div>
       {{employee.employeeid}}:{{employee.name}}
       <router-link class="secondary-content" v-bind:to="{ name: 'view-employee', params: { employeeid: employee.employeeid }}">
         <i class="fa fa-eye"></i>
       </router-link>
       </li>
     </ul>
     <div class="fixed-action-btn">
       <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
       </router-link>
     </div>
  </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "dashboard",
  data() {
    return {
      employee: []
    };
  },
  created() {
    db
      .collection("employee")
      .orderBy("department")
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          const data = {
            id: doc.id,
            employeeid: doc.data().employeeid,
            name: doc.data().name,
            department: doc.data().department,
            position: doc.data().position
          };
          this.employee.push(data);
        });
      });
  }
};
</script>



