<template>
  <div id="view-employee">
    <ul class="collection with-header">
      <li class="collection-header"><h4>{{name}}</h4></li>
      <li class="collection-item"><h3></h3>EmployeeID: {{employeeid}}</li>
      <li class="collection-item"><h3></h3>Department: {{department}}</li>
      <li class="collection-item"><h3></h3>Position: {{position}}</li>
    </ul>
    <router-link to="/" class="btn grey">Back</router-link>
    <button @click="deleteEmployee" class="btn red">Delete</button>
       <div class="fixed-action-btn">
      <router-link v-bind:to="{ name: 'edit-employee', params: { employeeid: employeeid }}" class="btn-floating btn-large red">
        <i class="fa fa-pencil"></i>
      </router-link>
</div>
  </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: 'view-employee',
  data ()  {
      return {
        employeeid: null,
        name: null,
        department: null,
        position: null          
      }
  },
  beforeRouteEnter: (to, from, next) => {
    db.collection('employee').where('employeeid','==', to.params.employeeid)
    .get()
    .then(querySnapshot => {
      querySnapshot.forEach(doc => {
        next(vm => {
          vm.employeeid = doc.data().employeeid
          vm.name = doc.data().name
          vm.department = doc.data().department
          vm.position = doc.data().position
        })
      })
    })
  },
  watch: {
    '$route': 'fetchData'
  },
    methods: {
      fetchData () {
        db.collection('employee').where('employeeid', '==', this.$route.params.employeeid).get().then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            this.employeeid = doc.data().employeeid
            this.name = doc.data().name
            this.department = doc.data().department
            this.position = doc.data().position
          })
        })
      },
      deleteEmployee () {
        if(confirm ('Are you sure?')) {
          db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
              doc.ref.delete()
              this.$router.push('/')
            })
          })
        }
      }
    }
  }
</script>



